@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
@import url(https://fonts.googleapis.com/css?family=Fira+Sans+Condensed);

body {
  font-family: "Open Sans", serif;
}

.wrapper {
  background: linear-gradient(45deg, rgb(12, 187, 149), #C5FFE4);
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.main {
  height: 90vh;
  background: #fff;
  box-shadow: 0px 1rem 2rem -1rem rgba(0,0,0,0.75);
  width: auto;
  transition: all 0.4s ease-in-out;
}

.main:hover {
  box-shadow: 0px 1rem 4rem -1rem rgba(0,0,0,0.75);
  transition: all 0.4s ease-in-out;
}

.container {
  margin: 0;
}

.title-container {
  height: 90vh;
  background: url(/iweather/static/media/landing1.0b8093e3.jpg) center center no-repeat;
  opacity: 0.97;
  background-size: cover;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  color: white;
  padding: 0 1rem;
  transition: all 0.2s ease-in-out;
}

.title-container:hover {
  opacity: 1;
  transition: all 0.2s ease-in-out;
}

.title-container__title {
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.title-container__subtitle {
  font-style: italic;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: "Merriweather", serif;
}

.form-container {
  background-color: #2c3e50;
  height: 90vh;
  padding: 5rem 2rem 0rem 3rem;
  color: #e0e0e0;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f16051;
  width: -webkit-fill-available;
  width: -moz-available;
  width: fill;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin: 1rem 1rem 1rem 0rem;
  font-size: 20px;
}

input[type="text"] { 
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
    -webkit-text-fill-color: #fff !important;
}

button {
  display: block;
  border: 0;
  margin: 1rem 0rem 3rem 0rem;
  padding: 8px 20px;
  border-radius: 50px;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 400;
  transition: all 0.2s ease-in-out;
}

button:hover {
  background-color: #eb4734;
  transition: all 0.2s ease-in-out;
}

button:active {
  outline: none;
}

.weather__info {
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}

.weather__key {
  color: #f16051;
  border-bottom: solid 2px rgba(255,255,255,0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #fff;
  font-weight: 200;
}

.weather__error {
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}
